import React from 'react';
import styles from './Tooltip.module.css';

const Tooltip = ({ content, display, children }) => {
  if (display) {
    return (
      <div className={styles.tooltip}>
        {children}
        <span className={styles.tooltipText}>{content}</span>
      </div>
    );
  }
  return null;
};

export default Tooltip;
